<template>
  <div class="center">
    <div class="center_breadcrumb">
      <Crumb @headInfo="headInfo" />
    </div>
    <div class="center_content">
      <div class="ability-query">
        <div class="ability-query_head">
          <img src="@/assets/img/bg-dachengdujisuan.png"/>
          <span>选择学期查看相应毕业要求的能力成长反馈</span>
        </div>
        <div class="ability-query_side">
          <ul>
            <li
              :class="{sideActive:sideActiveIndex === index}"
              @click="sideClick(vo,index) "
              v-for="(vo, index) in sideData"
              :key="index"
            >{{vo.grade + '-' + (vo.grade + 1)}}学年 {{vo.term === 1 ? '第一学期' : '第二学期'}}</li>
          </ul>
        </div>
        <div class="ability-query_content">
          <div class="top-div" v-if="tableData.length !==0">
             <ul class="top-ul">
              <li
                :class="{topActive:topActiveIndex === index}"
                @click="topClick(vo, index)"
                v-for="(vo,index) in tableData"
                :key="index"
              >{{vo.name}}</li>
            </ul>
          </div>
          <div class="bar-box">
               <p class="title-tips"><label>毕业要求：</label>{{nameTips}}</p>
               <series-bar id="sbilityQueryBar" :series-bar-data="seriesBarData" :isMax="true" :isMin="true" chartHeight="300px" />
          </div>       
          <div class="ability-query_content_body">
            <p class="title-tips"><label>毕业要求：</label>{{titleTips}}</p>
            <ul class="index-point">
              <li class="index-point_li" v-for="(vo ,index) in indexPointData" :key="index">
                <div class="index-point_left">{{vo.code}}</div>
                <div class="index-point_right">
                  <p>指标点{{vo.code}}</p>
                  <span>{{vo.content}}</span>
                  <div class="content">
                    <ul>
                      <li
                        :class="{textActive: num===0}"
                        v-for="(item,num) in vo.courseList"
                        :key="num"
                      >{{item.courseName}}</li>
                    </ul>
                </div>
                </div>
              </li>
            </ul>
          </div>
           <div class="feedback-information">
            <h4>能力反馈：</h4>
            <p>{{information}}</p>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import studentApi from "@/api/studentApi";
import SeriesBar from "../components/Echarts/SeriesBar";
export default {
  name: "AbilityQuery",
  components: { SeriesBar },
  data() {
    return {
      pageTitle: "能力查询反馈",
      sideActiveIndex: 0,
      topActiveIndex: 0,
      seriesBarData: {},
      titleTips: "",
      nameTips:'',
      page: {
        graduationId: "",
        year: "",
        term: ""
      },
      sideData: [],
      tableData: [],
      indexPointData: [],
      information:'',
      footCode:null,
       
    };
  },
  created() {},
  mounted() {
    this.yearGraduation();
  },
  methods: {
    yearGraduation() {
      const url = studentApi.yearGraduation;
      this.$get(url).then(res => {
        this.tableData = res.claims;
        this.sideData = res.yearTerms;
        this.page.year = res.yearTerms[0].grade;
        this.page.term = res.yearTerms[0].term;
        this.page.graduationId = res.claims[0].id;
        this.titleTips = `${res.claims[0].name}: ${res.claims[0].content}`;
        this.nameTips=`${res.claims[0].name}`
        this.getPointCourse();
        this.footCode=res.claims[0].code;
        this.courseSocreByGraduationId();
      });
    },
    //根据毕业要求和学年查询指标点达成
    courseSocreByGraduationId() {
      this.seriesBarData={}
      const url = studentApi.courseSocreByGraduationId;
      this.$get(url, this.page).then(res => {
          this.information= res.returnFeedback ? `针对毕业要求${this.footCode}的能力达成来看， ${res.returnFeedback}` : '暂未开始该能力的学习'
        //柱状图
        const top = ["我的分值", "平均值", "目标值"];
        const tip = res.name.split(",").map(item => {
          return `指标点${item}`;
        });
        const echartData = {
          legendData: top,
          xAxisData: tip,
          seriesData: top.map(item => {
            return {
              name: item,
              barGap: "0", //柱状图间距
              data: []
            };
          })
        };
        echartData.seriesData[0].data = res.ratio.split(",");
        echartData.seriesData[1].data = res.avgRatioStr.split(",");
        echartData.seriesData[2].data = res.targetRatioStr.split(",");
        this.seriesBarData = echartData;
        
      });
    },
    //获取学生指标点和课程
    getPointCourse() {
      const url = studentApi.getPointCourse;
      const postData = {
        graduationId: this.page.graduationId
      };
      this.$get(url, postData).then(res => {
        this.indexPointData = res;
      });
    },
    sideClick(vo, index) {
      this.sideActiveIndex = index;
      this.page.year = vo.grade;
      this.page.term = vo.term;
      this.courseSocreByGraduationId();
    },
    topClick(vo, index) {
      this.topActiveIndex = index;
      this.nameTips=vo.name
      this.page.graduationId = vo.id;
      this.titleTips = `${vo.name}: ${vo.content}`;
      this.footCode=vo.code
      this.courseSocreByGraduationId();
      this.getPointCourse();
    },
    headInfo(item) {
      this.pageTitle = item[1];
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
@import "../../styles/abilityQuery.scss";
</style>



